import React from 'react';

// Text design imports
import { withStyles } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';

// Modal imports
import Modal from '@material-ui/core/Modal';

// For form components
import Center from 'react-center';

// For Modal Styling
function getModalStyle() {
  const top = 50;
  const left = 50;

  return {
    top: `${top}%`,
    left: `${left}%`,
    transform: `translate(-${top}%, -${left}%)`,
  };
}

const styles = theme => ({
  paper: {
    position: 'absolute',
    width: theme.spacing.unit * 33,
    backgroundColor: theme.palette.background.paper,
    boxShadow: theme.shadows[5],
    padding: theme.spacing.unit * 4,
    textAlign: theme.center,
  },
});

class SimpleModal extends React.Component {
  render() {
    const { classes } = this.props;

    return (
      <div>
        <Modal
          open={this.props.ContactModalState}
          onClose={this.props.handleContactClose}
        >
            <div style={getModalStyle()} className={classes.paper}>
                <Center>
                    <Typography variant="display1">
                        Contributions
                    </Typography>
                </Center>
                <br />
                <Center>
                    <div style={{textAlign: "center"}}>
                        Your contibutions will make a huge difference. 
                        To join the community contact:
                    </div>
                </Center>
                <br />
                <Center>
                    ********@****.com
                </Center>
            </div>
        </Modal>
    </div>
    );
  }
}

const ContactModal = withStyles(styles)(SimpleModal);

export default ContactModal;


