import React from 'react';

// Styling for text
const aboutText = {
    fontSize: "20px",
    lineHeight: "1.4"
}

const aboutHead = {
    fontSize: "30px"
}

const AboutContent = (props) => {
    return (
        <div style={aboutText}>
            <span style={aboutHead}>What is ********?</span><br /><br />
            ********* is just another tutorial site that you might find in the internet. 
            It is meant to have quality developer content for every aspiring developer.
            <br /><br />

            <span style={aboutHead}>Is the content free?</span><br /><br />
            It's a non-profit community.
            <br /><br />

            <span style={aboutHead}>How are we Unique?</span><br /><br />
            We are a non-profit community dedicated to provide quality educational content through the internet. Well, that doesnt make us unique, does it? There are various other communities available in the internet which do the same.
            <br /><br />
            What makes us different is that, even you can teach in here! It will always be a better environment when content is broadcasted by a regular guy than by a team from the website.
            <br /><br />
            Hence, we have created a non-profit platform where people could share all their teaching content.
            <br /><br />
        </div>
    );
}

export default AboutContent;


