import React from 'react';

import Typography from '@material-ui/core/Typography';
import { withStyles } from '@material-ui/core/styles';

const footerStyle = {
    position: "relative",
    bottom: "0",
    width: "100%",
    textAlign: "center",
    paddingTop: "20px",
    paddingBottom: "20px",
    backgroundColor: "#3f51b5",
    textColor: "white"
};

const styles = {
    typoGraphy: {
        color: 'white',
    },
};

const Footer = (props) => {
    return (
        <div style={footerStyle}>
            <Typography component="p" className={props.classes.typoGraphy}>
                ©*********
            </Typography>
        </div>
    );
}

export default withStyles(styles)(Footer);
    


